
.navbar{
  position: fixed;   
  top: 0; left: 0; right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  padding:1rem 9%;
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .1);
  
}

.navbar img{
   width:5.5rem ;
   height:5.5rem;
}

/* .navbar .logo{
    font-size: 2.5rem;
    font-weight: bolder;
    color: #666;
   
}
.navbar .logo i{
padding-right:.5rem ;
color: var(--red);
} */
.navbar .links a{
font-size:2rem;
margin-left:2rem;
color: #666;
}
.navbar .links a:hover{
color: var(--red);
}
.menu-bar{
font-size:2.7rem;
cursor: pointer;
color: #666;
border:.1rem solid #666;
border-radius: .3rem;
padding:.5rem 1.5rem;
display: none;
}

































  @media(max-width:991px){
      html{
        font-size:55%
      }
      .navbar{
          padding: 2rem;
      }
      .home{ 
        padding: 2rem; 
     }
  }















  @media(max-width:768px){
    .menu-bar{
        display: initial;
       
    }
    /* .navbar{
      background-color: seagreen;
    } */
    .navbar .links{
        position: absolute;
        top: 100%; left: 0; right: 0;
        background: #f7f7f7;
        border-top:.1rem solid rgba(0, 0, 0, .1);
        clip-path:polygon(0 0,100% 0,100% 0, 0 0);
    }
    .linkmobileactive{
      clip-path:polygon(0 0,100% 0,100% 100%, 0% 100%) !important;
    }
    .navbar .links a{
        margin:1.5rem;
        padding:1.5rem; 
        display: block;
        border: .2rem solid rgba(0, 0, 0, .1);
        border-left:.5rem solid var(--red);
        background: #fff;

    }
  }










  @media(max-width:450px){
    html{
        font-size:50%
    }
    .navbar{
        padding:0.5rem 4rem;
    }
   
}


