.order{
    padding:3rem;
}
.order .row{
    padding: 2rem;
    box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 1);
    background: #fff;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    border-radius: .5rem;
}
 .order .row .image{ 
    flex: 1  1 30rem;
}
 .order .row .image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius:.5rem; 
}
 .order .row form{
    flex: 1  1 50rem;
    padding: 1rem;
}
.order .row  form  .inputbox{ 
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.order .row   form .inputbox  input, .order .row  form  textarea{
  padding:1rem;
  margin:1rem;
  font-size: 1.7rem;
  color: #333;
  text-transform:none;
  border: .1rem solid rgba(0, 0, 0, .3);
  border-radius: .5rem;
  width:45%;
}
.order .row form  textarea{
    width: 100%;
    resize: none;
    height: 15rem;
}
.order .row   form  .btn{
    background: none;
}
.order .row  form .btn:hover{
    background: var(--red);
}




@media(max-width:450px){
    .order .row form .inputbox input, .order .row form textarea{
     width:100%;

        
    }
}





