 .home{ 
   padding: 2rem 9%; 
}
.home{
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    min-height: 100vh;
    align-items: center;
    background:url(../image/home-bg.jpg)no-repeat ;
    background-size:cover;
    background-position:center;
}
.home .content{
    flex: 1 1 40rem;
    padding-top: 6.5rem;
    
}
.home .image{
    flex: 1 1 40rem;
}
.home .image img{
    width: 100%;
    padding: 1rem;
    animation: float 3s linear infinite; 
}
@keyframes float{
    0%, 100%{
        transform: translateY(0rem);
    }
    50%{
        transform: translateY(3rem);
    }
}
.home .content h3{
    font-size:5rem;
    color: #333;
}
.home .content p{
    font-size:1.7rem;
    color: #666;
    padding: 1rem 0;
    text-transform: none;
}
.btn{
    display: inline-block;
    padding: .8rem 3rem;
    border: .2rem solid var(--red);
    color:var(--red);
    cursor: pointer;
    font-size: 1.7rem;
    border-radius: .5rem;
    position: relative;
    overflow: hidden;
    z-index: 0;
    margin-top: 1rem;
}
.btn::before{
    content: '';
    position: absolute;
    top: 0; right: 0;
    width:0%;
    height: 100%;
    background-color: var(--red);
    transition: .3s linear;
    z-index: -1;
}
.btn:hover::before{
    width: 100%;
    left: 0;
}
.btn:hover{
    color: #fff;
}